import React from "react";

import Particles from "react-particles-js";

import "../../assets/css/pages/whatIdo.css";

const WhatIdoView = ({ knowledge }) => {
	return (
		<>
			<Particles
				params={{
					particles: {
						number: {
							value: 55,
							density: {
								enable: true,
								value_area: 1900,
							},
						},
						color: {
							value: "#ffffff",
						},
						shape: {
							type: "circle",
							stroke: {
								width: 0,
								color: "#000000",
							},
							polygon: {
								nb_sides: 5,
							},
							image: {
								src: "img/github.svg",
								width: 100,
								height: 100,
							},
						},
						opacity: {
							value: 0.5,
							random: false,
							anim: {
								enable: false,
								speed: 1,
								opacity_min: 0.1,
								sync: false,
							},
						},
						size: {
							value: 3,
							random: true,
							anim: {
								enable: false,
								speed: 40,
								size_min: 0.1,
								sync: false,
							},
						},
						line_linked: {
							enable: true,
							distance: 173.61248115909999,
							color: "#ffffff",
							opacity: 0.3,
							width: 1,
						},
						move: {
							enable: true,
							speed: 1,
							direction: "none",
							random: false,
							straight: false,
							out_mode: "out",
							bounce: false,
							attract: {
								enable: false,
								rotateX: 600,
								rotateY: 1200,
							},
						},
					},
					interactivity: {
						detect_on: "canvas",
						events: {
							onhover: {
								enable: true,
								mode: "grab",
							},
							onclick: {
								enable: true,
								mode: "repulse",
							},
							resize: true,
						},
						modes: {
							grab: {
								distance: 400,
								line_linked: {
									opacity: 1,
								},
							},
							bubble: {
								distance: 400,
								size: 40,
								duration: 2,
								opacity: 8,
								speed: 3,
							},
							repulse: {
								distance: 200,
								duration: 0.4,
							},
							push: {
								particles_nb: 4,
							},
							remove: {
								particles_nb: 2,
							},
						},
					},
					retina_detect: true,
				}}
			/>
			<section
				id="whatIdo"
				className="page p-4 position-relative vh-100 d-flex justify-content-center align-items-center flex-wrap"
			>
				<div className="page-head position-relative d-flex align-items-center justify-content-center w-100 ">
					<h2 className="position-absolute text-background title m-0 font-weight-bold text-center ">
						LO QUE HAGO
					</h2>
					<h3 className="subTitle m-0 font-weight-bold text-center text-white">
						Tecnologias que utilizo
					</h3>
				</div>
				<div className="page-body d-flex justify-content-center align-items-center flex-wrap w-100 mt-4">
					<div className="row">
						{knowledge.map((item, index) => {
							return (
								<div className="col-12 col-sm-12 col-md-12 col-lg-6 mb-4" key={index}>
									<div className="h-100 p-2 d-flex flex-wrap justify-content-center justify-content-lg-start">
										<figure className="m-0 mb-3 text-center p-2 pr-0 pl-0">{item.icon}</figure>
										<div className="technology">
											<h4 className="text-center w-100 text-white">{item.technologyType}</h4>
											{item.technologyArray.map((technology, indexTechnology) => {
												return (
													<p className="m-0 text-white" key={indexTechnology}>
														{technology}
													</p>
												);
											})}
										</div>
									</div>
								</div>
							);
						})}
					</div>
				</div>
			</section>
		</>
	);
};

export default WhatIdoView;
